@import url(https://fonts.googleapis.com/css?family=K2D);
* {
    font-family: K2D;
}

body {
    background: #333;
    margin: 0;
    padding: 0;
    /* font-family: "Audiowide", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.App {
    text-align: center;
    background-color: #333;
    color: #ccc;
}

.App .pane {
    background-color: #555;
}

.App .row .pane-title {
    text-transform: uppercase;
    padding: 0 1rem;
    margin-bottom: 0.5rem;
    background-color: #f55;
    text-align: left;
}

.App .container {
    margin: 10px 0;
    padding-bottom: 10px;
    /* padding-top: 10px; */
}

.Planner {
    font-size: 80%;
    /* background-color: #777; */
}

.App .current-step {
    background-color: #f55;
}


